a {
  cursor: pointer;
  text-decoration: underline;
}

.ui-dialog-overlay {
  background: #aaaaaa;
  opacity: 0.3;
  filter: Alpha(Opacity=30);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}

.ui-dialog {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) !important;
      -ms-transform: translate(-50%, -50%) !important;
          transform: translate(-50%, -50%) !important;
  outline: 0 none;
  padding: 0 !important;
  z-index: 101;
  background-color: white;
  border: 1px solid #f6f6f6;
}

.ui-dialog.maximized {
  position: fixed;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
}

.ui-dialog.minimized {
  position: fixed;
  bottom: 0;
  right: 0;
}

.ui-dialog .ui-dialog-titlebar {
  position: relative;
  font-size: 1em;
  border-radius: 3px;
  padding: 0.5em;
  border-bottom: 1px solid #f6f6f6;
}

.ui-dialog .ui-dialog-titlebar:after {
  content: ' ';
  display: table;
  clear: both;
}

.ui-dialog.react-draggable .ui-dialog-titlebar {
  cursor: move;
}

.ui-dialog .ui-dialog-titlebar .action-items {
  float: right;
  position: relative;
}

.ui-dialog .ui-dialog-titlebar .title {
  float: left;
  margin-right: 0.5em;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5em;
}

.icon {
  width: 24px;
  height: 24px;
  display: block;
  float: left;
  margin: 5px;
  cursor: pointer;
  background-size: cover;
}

.icon.icon-close {
  width: 20px;
  height: 20px;
  background-image: url('data:image/png;base64,');
}

/* .icon.icon-minimize {
    background-image: url("./img/minimize.png");
}

.icon.icon-maximize {
    background-image: url("./img/maximize.png");
}

.icon.icon-restore {
    background-image: url("./img/restore.png");
} */

.ui-dialog .ui-dialog-content {
  background: none repeat scroll 0 0 transparent;
  border: 0 none;
  overflow: auto;
  position: relative;
  padding: 0.5em;
  width: 100%;
  height: calc(100% - 47px);
}

.ui-dialog .ui-dialog-buttonpane {
  position: absolute;
  width: 100%;
  bottom: 0;
  text-align: right;
  border-width: 1px 0 0 0;
  border-top: 1px solid #f6f6f6;
}

.ui-dialog .ui-dialog-buttonpane .ui-dialog-buttonset {
  padding: 0.5em;
}

.ui-dialog .ui-dialog-buttonpane button {
  margin: 0 0.5em;
  cursor: pointer;
  background-color: #f6f6f6;
  padding: 0.5em 1em;
  outline: none;
  border: 1px solid #cccccc;
  border-radius: 3px;
}

.ui-dialog .ui-dialog-buttonpane button:hover {
  background-color: #cccccc;
  border: 1px solid #bbbbbb;
}

.ui-dialog .react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  bottom: 0;
  right: 0;
  background: url('data:image/svg+xml;base64,');
  background-position: bottom right;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: se-resize;
}

.stimuli-option {
  width: 33%;
  padding-left: 8px;
  padding-right: 8px;
  cursor: pointer;
  text-align: center;
}

.stimuli-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 100%;
}

.stimuli-option img {
  width: 50%;
}



