.react-simple-input-container {
  position: relative;
}
.react-simple-input-container .react-simple-input {
  padding: 6px 12px;
  border: 1px solid #c7c7c7;
  color: #555;
}
.react-simple-input-container .react-simple-input-clear {
  position: absolute;
  right: 8px;
  top: 4px;
  font-size: 16px;
  color: #777;
}

.react-simple-input-container > input {
  width: 500px;
  height: 50px;
  cursor: 50px;
  font-size: 40px;
}

.ui-dialog-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 100%;
}



